<template>
  <div class="flex flex-col justify-center items-center pt-14 gap-y-7">
    <div class="text-3xl font-bold">
      Decentralized Dapp Store
    </div>
    <div class="text-sm font-serif p-5 w-3/5">
      You can register your new dapp below, the url is the link of your dapp’s logo, name is the name of your dapp, description is the description of your dapp, you need to use metamask,
      And set up the bsc network to submit, this link is submitted to the bsc network, not the eth network
<!--        你可以在下面注册你的新的dapp，url是你的dapp的logo的链接，name是你的dapp的名字，description是你的dapp的相关描述,需要使用metamask，-->
<!--      并且设置好bsc的网络才能提交，这个链接是提交到bsc的网络上面，不是eth的网络-->
    </div>

    <div class="w-3/5">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="url" prop="">
          <el-input type="text" v-model="ruleForm.url"></el-input>
        </el-form-item>
        <el-form-item label="logoimg" prop="">
          <el-input type="text" v-model="ruleForm.logoimg"></el-input>
        </el-form-item>
        <el-form-item label="name" prop="">
          <el-input type="text" v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="description">
          <el-input v-model.number="ruleForm.description" type="textarea"></el-input>
        </el-form-item>
        <el-form-item class="inline-block text-center">
          <el-button  type="primary" @click="submitForm('ruleForm')">提交</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>

<script>
import DappStore from "../utils/DappStore.json"
export default {
  name: "HomePage",
  created:async function(){
    // console.log(this.ethers)
    await window.ethereum.enable()
    const provider = new this.ethers.providers.Web3Provider(window.ethereum)
    this.$store.dispatch("iniP",{
      provider:provider
    })
    let contractAddress="0x9B8a3520e6b3678C0d393dbB3478Ae3E4177D1d5"
    let contract = new this.ethers.Contract(contractAddress, DappStore.abi, provider)
    console.log(contract)
    this.$store.dispatch("iniC",{
      contract:contract
    })
    console.log("this.$store.state.contract:",this.$store.state.contract)
    let userAddress=await provider.getSigner().getAddress()
    console.log(await contract.name())
    this.$store.dispatch("iniU",{
      userAddress:userAddress
    })
    this.money=await contract.balanceOf(userAddress)
    let singer=await provider.getSigner()
    this.signedContract=contract.connect(singer)
    this.$store.dispatch("iniS",{
      signedContract:this.signedContract
    })
    // this.$store.state.a="sdafs"
  },
  data() {
    return {
      ruleForm: {
        url: '',
        name: '',
        logoimg:"",
        description: ''
      },
      signedContract: null
    }
  },
  methods:{
      async submitForm(formName) {
        let name=this.ruleForm.name
        let url=this.ruleForm.url
        let description=this.ruleForm.description
        let logoimg=this.ruleForm.logoimg
        console.log(this.signedContract)

        await this.signedContract.create_dapp(url,name,description,logoimg)
        this.resetForm("ruleForm")
        this.$message({
          message: '恭喜你，添加dapp到网站成功',
          type: 'success'
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
}
</script>

